<template>
<div>
  <div>
    <div class="crumbs">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item><i class="el-icon-menu"></i></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-container>
      <el-main style="padding-left: 0px;padding-top: 0px;margin-top:30px;">
        <!--  -->
        <div v-show="nowTab==1">
          <el-tabs v-model="activeName">
   <el-tab-pane label="" name="first">
     <div style="margin-top: 20px">
     <el-steps :active="nowTab">
       <el-step title="" icon="el-icon-edit"></el-step>
       <el-step title="" icon="el-icon-document"></el-step>
     </el-steps>
     </div>
     <el-card style="margin-top:30px;">
       <el-form :model="ruleForm" ref="ruleForm" :rules="rules" label-width="110px" class="demo-ruleForm" style="width:40%">
         <div>
           <el-form-item label="" prop="projectId" required>
             <el-select v-model='ruleForm.projectId' placeholder="">
               <el-option v-for="item in restaurants3" :key="item.value" :label="item.label" :value="item.value"></el-option>
             </el-select>
           </el-form-item>
           <el-form-item label="" prop="clientCompanyName">
             <el-input v-model="ruleForm.clientCompanyName"></el-input>
           </el-form-item>
           <el-form-item label="" prop="contactPerson">
             <el-input v-model="ruleForm.contactPerson"></el-input>
           </el-form-item>
           <el-form-item label="" prop="contactPhone">
             <el-input v-model="ruleForm.contactPhone"></el-input>
           </el-form-item>
           <el-form-item label="" prop="carType">
             <el-input v-model="ruleForm.carType"></el-input>
           </el-form-item>
           <el-form-item label="" prop="tax">
             <el-input-number v-model="ruleForm.tax" controls-position="right" :min="0"></el-input-number>
           </el-form-item>
           <el-form-item label="" prop="remark">
             <el-input type="textarea" v-model="ruleForm.remark"></el-input>
           </el-form-item>
         </div>

       </el-form>
       <div class="card-bottom" style="float:right;">
         <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
       </div>
     </el-card>
   </el-tab-pane>
   <el-tab-pane label="" name="second">
     <el-card style="margin-top:30px;">
       <el-form :model="fujianForm" ref="fujianForm" :rules="rules" label-width="110px" class="demo-ruleForm" style="width:40%">
         <div>
           <el-form-item label="" prop="projectId" required>
             <el-select v-model='fujianForm.projectId' placeholder="">
               <el-option v-for="item in restaurants3" :key="item.value" :label="item.label" :value="item.value"></el-option>
             </el-select>
           </el-form-item>
           <el-form-item label="" prop="clientCompanyName">
             <el-input v-model="fujianForm.clientCompanyName"></el-input>
           </el-form-item>
           <el-form-item label="" prop="contactPerson">
             <el-input v-model="fujianForm.contactPerson"></el-input>
           </el-form-item>
           <el-form-item label="" prop="contactPhone">
             <el-input v-model="fujianForm.contactPhone"></el-input>
           </el-form-item>
           <el-form-item label="" prop="carType">
             <el-input v-model="fujianForm.carType"></el-input>
           </el-form-item>
           <el-form-item label="" prop="noTaxTotal" required>
             <el-input-number v-model="fujianForm.noTaxTotal" controls-position="right" :min="0"></el-input-number>
           </el-form-item>
           <el-form-item label="" prop="tax">
             <el-input-number v-model="fujianForm.tax" controls-position="right" :min="0"></el-input-number>
           </el-form-item>
           <el-form-item label="" prop="tax" required>
             <span><el-tag type="warning" > {{totalPrice}} </el-tag></span>
           </el-form-item>
           <el-form-item label="" prop="remark">
             <el-input type="textarea" v-model="fujianForm.remark"></el-input>
           </el-form-item>
           <el-form-item label="" >
             <div v-show="wen_jian_url !== ''">
               <p style="color: blue;"></p>
             </div>
             <div v-show="wen_jian_url === ''">
                 <vue-core-image-upload :class="['el-button', 'el-button--primary']"
                 :crop="false"
                  inputOfFile="imageFile"
                  :url="upload"
                 extensions="png,ppt,docx,txt,jpg,xlsx,pdf"
                 :max-file-size="5242880"
                 :data="wenjianData" text=""
                 :multiple="true"
                 :multiple-size="30"
                 credentials="true"
                 @imageuploaded="imageupwenjian" @errorhandle="handleError">
                 </vue-core-image-upload>
             </div>
           </el-form-item>
         </div>
       </el-form>
       <div class="card-bottom" style="float:right;">
         <el-button type="primary" @click="tiJiao('ruleForm')"> </el-button>
       </div>
     </el-card>
   </el-tab-pane>
 </el-tabs>
        </div>
        <!--  -->
        <div v-show="nowTab==2">
          <el-card class="box-card" style="margin-top:25px;" v-loading="table_loading">
            <div slot="header" class="clearfix">
              <span></span>
              <div class="card-bottom" style="float: right;">
                <el-button type="primary" class="el-icon-plus" @click="toAadd"></el-button>
              </div>
            </div>
            <div>
              <el-table :data="productData" border style="width: 100%">
                <el-table-column fixed prop="productName" label="">
                </el-table-column>
                <el-table-column prop="" label="">
                  <template slot-scope="scope">
              <img class="image" v-bind:src="scope.row.picUrls[0]" style="height:30px;width:30px;">
            </template>
                </el-table-column>
                <el-table-column prop="wenlidaima" label="">
                </el-table-column>
                <el-table-column prop="price" label="">
                </el-table-column>
                <el-table-column prop="productNum" label="">
                </el-table-column>
                <el-table-column prop="noTaxPrice" label="">
                </el-table-column>
                <el-table-column prop="total" label="">
                </el-table-column>
                <el-table-column prop="area" label="">
                </el-table-column>
                <el-table-column fixed="right" label="" width="300">
                  <template slot-scope="scope">
       <el-button type="text" size="small" @click.native.prevent="toEidtor(scope.$index, productData)"></el-button>
       <el-button
         @click.native.prevent="deleteRow(scope.$index, productData)"
         type="text"
         size="small">
         
       </el-button>
     </template>
                </el-table-column>
              </el-table>
            </div>
            <div style="margin-top:20px;margin-bottom:20px;">
              <div class="card-bottom" style="float: left;">
                <el-button type="primary" @click="nowTab = 1"></el-button>
              </div>
              <div class="card-bottom" style="float: right;">
                <el-button type="primary" @click="commit"> </el-button>
              </div>
            </div>
          </el-card>
        </div>
        <!--  -->
        <div>
          <el-dialog width="60%" title="" :visible.sync="innerVisible" :close-on-click-modal="false" append-to-body>
            <el-form ref="elform" :model="elform" :rules="rules" label-width="120px" style="width:100%;">
              <el-form-item label="" prop="productName">
                <el-input v-model="elform.productName" style="width:30%;" placeholder=""></el-input>
              </el-form-item>
              <el-form-item label="" prop="wenlidaima">
                <el-input v-model="elform.wenlidaima" style="width:30%;" placeholder=""></el-input>
              </el-form-item>
              <el-form-item label="" prop="productNum">
                <el-input-number v-model="elform.productNum" controls-position="right" :min="1"></el-input-number>
              </el-form-item>
              <el-form-item label="" prop="area">
                <el-input-number v-model="elform.area" controls-position="right" :min="0  "></el-input-number>
              </el-form-item>
              <el-form-item label="">
                <el-switch v-model="elform.dongMo"></el-switch>
              </el-form-item>
              <el-form-item label="" prop="coefficient">
                <el-input v-model="elform.coefficient" style="width:30%;" placeholder=""></el-input>
              </el-form-item>
              <el-form-item label="" prop="price">
                <el-input-number v-model="elform.price" controls-position="right" :min="0"></el-input-number>
              </el-form-item>
              <el-form-item label="">
                <div>
                  <vue-core-image-upload :crop="false" inputOfFile="imageFile" :url="upload" extensions="png,gif,jpeg,jpg" :class="['el-button', 'el-button--primary']" :max-file-size="5242880" :data="imageData" text="" :multiple="true" :multiple-size="30" credentials="true"
                    @imageuploaded="imageuploaded" @errorhandle="handleError">
                  </vue-core-image-upload>
                </div>
              </el-form-item>
            </el-form>
            <div style="border: 1px dashed #d9d9d9;width:100%;min-height:358px;">
              <el-row :gutter="20" style="margin-top:10px;">
                <el-col :span="6" v-for="imageUrl in imageUrls ">
                  <el-card :body-style="{ padding: '0px' }" class="mould-card">
                    <img class="image" v-bind:src="imageUrl" style="height:320px;">
                    <div style="text-align:center;">
                      <el-button type="text" @click="delMouldImage(imageUrl)"><i class="el-icon-error" style="color:red;"></i></el-button>
                    </div>
                  </el-card>
                </el-col>
              </el-row>
            </div>
            <div slot="footer" class="dialog-footer">
              <el-button type="primary" v-show="!udapteBoolean" @click="toAddProduct"> </el-button>
              <el-button type="primary" v-show="udapteBoolean" @click="toUpdateProduct"> </el-button>
              <el-button @click="chanel"> </el-button>
            </div>
          </el-dialog>
        </div>
      </el-main>
    </el-container>
  </div>
</div>
</template>
<script>
import VueCoreImageUpload from 'vue-core-image-upload';
import JLApiUtils from '../../utils/JLApiUtils.js';
export default {
  components: {
    VueCoreImageUpload
  },
  props: {
    title: String
  },
  data() {
    return {
      wen_jian_url:'',
      upload:'',
      fujianForm:{
        tax: 16,
        noTaxTotal:"0"
      },
      udapteBoolean: false,
      activeName: 'first',
      itemIndex: '',
      wenjianData: {
          userId: '',
          imgPakage: 'yu_bao_jia'
      },
      itemRow: {},
      imageData: {
        userId: '',
        imgPakage: 'yu_bao_jia'
      },
      table_loading: false,
      imageUrls: [],
      ruleForm: {
        tax: '16',
        addUserId: '',
        addUserName: ''
      },
      elform: {
        productNum:'1',
        area:'0',
        price:'0',
        coefficient:'1',
        dongMo: false
      },
      productData: [],
      restaurants3: [],
      innerVisible: false,
      tableData: [],
      select_word: '',
      nowTab: 1,
      rules: {
        productName: [{
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 0,
            max: 255,
            message: ' 0  255 ',
            trigger: 'blur'
          }
        ],
        projectId: [{
            required: true,
            message: '',
            trigger: 'blur'
          }
        ],
        wenlidaima: [{
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 0,
            max: 255,
            message: ' 0  255 ',
            trigger: 'blur'
          }
        ],
        carType: [{
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 1,
            max: 255,
            message: ' 1  255 ',
            trigger: 'blur'
          }
        ],
        clientCompanyName: [{
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 1,
            max: 255,
            message: ' 1  255 ',
            trigger: 'blur'
          }
        ],
        contactPerson: [{
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 1,
            max: 255,
            message: ' 1  255 ',
            trigger: 'blur'
          }
        ],
        contactPhone: [{
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 1,
            max: 255,
            message: ' 1  255 ',
            trigger: 'blur'
          }
        ],
        paytype: [{
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 1,
            max: 255,
            message: ' 1  255 ',
            trigger: 'blur'
          }
        ],
        deliverytime: [{
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 1,
            max: 255,
            message: ' 1  255 ',
            trigger: 'blur'
          }
        ],
        remark: [{
          min: 0,
          max: 2048,
          message: ' 0  2048 ',
          trigger: 'blur'
        }]
      },
    }
  },
  methods: {
    tiJiao(){
      let self = this
      if(this.wen_jian_url == '' || this.wen_jian_url.length <=0){
        this.$message.error("");
        return;
      }
      let parmas = self.fujianForm;
      parmas.addUserId = this.uid;
      parmas.addUserName = this.name;
      parmas.total = this.totalPrice;
      parmas.submitType = 1;
      parmas.fuJianUrl = this.wen_jian_url;
      let dataParam = {
        businessProductList: [],
        businessBaoJia:parmas
      }
      this.jsonPostRequest("/offer/bus/add", dataParam).then(resp => {
        if (resp && resp.status == 200 && resp.data.code == 0) {
          self.$message.success("");
          this.$router.push('/offer/bus/list');
        } else {
          self.$message.error("");
        }
      })
    },
    imageupwenjian(data) {
      this.$message.success("");
      let urls = [];
      urls = data.url;
      if(urls != 'undefinded' && urls.length > 0 ){
        this.wen_jian_url = urls[0];
      }
    },
    toAadd(){
      this.innerVisible = true;
      this.udapteBoolean = false;
      this.elform = {
          productNum:'1',
          area:'0',
          price:'0',
          coefficient:'1',
          dongMo: false
        },
    //  this.resetForm('elform');
      this.imageUrls=[]
    },
    chanel(){
      this.innerVisible = false;
      this.udapteBoolean = false;
    },
    commit() {
      let self = this;
      let products = self.productData;
      if (products == 'undefined' || products.length <= 0) {
        this.$message.error("")
        return;
      }
      self.addYuBaoJia();
    },
    addYuBaoJia() {
      let self = this;
      let parm = self.ruleForm;
      parm.addUserId = this.uid;
      parm.addUserName = this.name;
      parm.submitType = 0;

      this.table_loading = true;
      let parms = self.productData;
      if('undefined' == parms || parms.length <=0 ){
        this.$message.error("");
        return
      }
      let data = [];
      let dataUrl = "";
      let total = 0;
      let noTaxTotal = 0;
      let products = [];
      for (var i = 0; i < parms.length; i++) {
        let item = parms[i];
        item.addUserId = this.uid;
        item.addUserName = this.name;
        total = total + item.total;
        noTaxTotal = noTaxTotal + item.noTaxPrice;
        item.picUrls = this.toPinjieUrl(item.picUrls);
        products.push(item);
      }
      parm.total = total;
      parm.noTaxTotal = noTaxTotal;
      let dataParam = {
        businessProductList: products,
        businessBaoJia:parm
      }
      this.jsonPostRequest("/offer/bus/add", dataParam).then(resp => {
        if (resp && resp.status == 200 && resp.data.code == 0) {
          self.$message.success("");
          this.$router.push('/offer/bus/list');
        } else {
          self.$message.error("");
        }
      })
    },
    addProduct(id) {
      this.table_loading = true;
      let self = this;
      let parm = self.productData;
      let data = [];
      let dataUrl = "";
      for (var i = 0; i < parm.length; i++) {
        let item = parm[i];
        item.addUserId = this.uid;
        item.addUserName = this.name;
        item.yuBaoJiaId = id;
        item.picUrls = this.toPinjieUrl(item.picUrls);
        let pageinfoString = JSON.stringify(item);
        dataUrl = dataUrl + pageinfoString + "|";
      }
      let toParm = {
        yuBaoJiaId: id,
        yuProducts: dataUrl
      }
      this.postRequest("/offer/bus/add/product", toParm).then(resp => {
        this.table_loading = false;
        if (resp && resp.status == 200 && resp.data.code == 0) {
          self.$message.success("");
          this.$router.push('/offer/bus/list');
        } else {
          self.$message.error("");
        }
      })
    },
    deleteRow(index, rows) {
       rows.splice(index, 1);
     },
    toPinjieUrl(urls) {
      var te = this.$options.filters['imageFilter'];
      let url = te(urls);
      return url;
    },
    toEidtor(index, rows) {
      let self = this;
      self.udapteBoolean = true;
      let item = [];
      item = rows;
      self.itemRow = item[index];
      self.elform = self.itemRow;
      self.itemIndex = index;
      self.imageUrls = self.itemRow.picUrls;
      self.innerVisible = true;
    },
    toUpdateProduct(){
      let self = this;
      let taxI = self.ruleForm.tax / 100;
      let gaiL = taxI + 1;
      let total = self.elform.price * self.elform.productNum * gaiL;
      var te = this.$options.filters['toDecimal'];
      total = te(total);
      let product = {
        yuBaoJiaId: '',
        productName: self.elform.productName,
        wenlidaima: self.elform.wenlidaima,
        productNum: self.elform.productNum,
        area: self.elform.area,
        dongMo: self.elform.dongMo,
        coefficient: self.elform.coefficient,
        price: self.elform.price,
        picUrls: self.imageUrls,
        tax: self.ruleForm.tax,
        noTaxPrice: self.elform.price * self.elform.productNum,
        total: total,
        immutable: false,
        addUserId: this.uid,
        addUserName: this.name
      }
      let newData = [];
      for(let i = 0; i< this.productData.length; i++){
        if(i == this.itemIndex){
          newData.push(product);
        }else{
          newData.push(this.productData[i]);
        }
      }
      this.$nextTick(function() {
        this.productData = newData;
      })
      self.imageUrls = [];
      self.elform = {
          productNum:'1',
          area:'0',
          price:'0',
          coefficient:'1',
          dongMo: false
        },
      //self.resetForm('elform');
      self.innerVisible = false;
    },
    toAddProduct() {
      let self = this;
      let taxI = self.ruleForm.tax / 100;
      let gaiL = taxI + 1;
      let total = self.elform.price * self.elform.productNum * gaiL;
      var te = this.$options.filters['toDecimal'];
      total = te(total);
      let product = {
        yuBaoJiaId: '',
        productName: self.elform.productName,
        wenlidaima: self.elform.wenlidaima,
        productNum: self.elform.productNum,
        area: self.elform.area,
        dongMo: self.elform.dongMo,
        coefficient: self.elform.coefficient,
        price: self.elform.price,
        picUrls: self.imageUrls,
        tax: self.ruleForm.tax,
        noTaxPrice: self.elform.price * self.elform.productNum,
        total: total,
        immutable: false,
        addUserId: this.uid,
        addUserName: this.name
      }
      self.productData.push(product);
      self.imageUrls = [];
      self.elform = {
          productNum:'1',
          area:'0',
          price:'0',
          coefficient:'1',
          dongMo: false
        },
      //self.resetForm('elform');
      self.innerVisible = false;
    },
    // 
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    // 
    submitForm(formName) {
      let self = this;
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.nowTab = 2;
        } else {
          self.$message.error('');
          return false;
        }
      });
    },
    imageuploaded(data) {
      let self = this;
      if (data.url.length !== 'undefined' && data.url.length > 0) {
        let url = data.url
        let urls = self.imageUrls;
        for (var i = 0; i < url.length; i++) {
          urls.push(url[i]);
        }
        self.imageUrls = urls;
      }
    },
    delMouldImage(url) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.todelImage(url);
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
    todelImage(url) {
      let self = this
      if (url == 'undefined' || url.length == "") {
        self.$message.error("");
        return
      }
      this.getRequest("/config/delfile/delImage?url=" + url).then(resp => {
        if (resp && resp.status == 200 && resp.data.code == 0) {
          self.$message.success("");
          let urls = self.imageUrls;
          let newUrls = [];
          for (var i = 0; i < urls.length; i++) {
            if (url != urls[i]) {
              newUrls.push(urls[i])
            }
          }
          self.imageUrls = newUrls;
        } else {
          self.$message.error("");
        }
      })

    },
    loadAllProject() {
      let self = this
      this.getRequest("/offer/bus/list/project?addUserId=" + this.uid).then(resp => {
        if (resp && resp.status == 200 && resp.data.code == 0) {
          let item = []
          let re = resp.data.data.projectlist;
          if (re != 'undefined' && re.length > 0) {
            for (var i = 0; i < re.length; i++) {
              let elem = {
                label: '',
                value: ''
              }
              elem.label = re[i].projectName
              elem.value = re[i].id
              item.push(elem);
            }
            self.restaurants3 = item;
          }
        } else {
          console.log("");
        }
      });
    },
    // 
    handleError() {
      this.$notify.error({
        title: '',
        message: ''
      });
    }
  },
  mounted: function() {
    this.$nextTick(function() {
      this.imageData.userId = this.uid = localStorage.getItem('cp_uid');
      this.wenjianData.userId = this.uid = localStorage.getItem('cp_uid');
      this.upload = JLApiUtils.upload;
    })
    this.username = localStorage.getItem('cp_username');
    this.uid = localStorage.getItem('cp_uid');
    this.name = localStorage.getItem('cp_name');
    this.loadAllProject();
  },
  computed: {
     totalPrice: function() {
        var total = 0;
        total = this.fujianForm.noTaxTotal * (100+this.fujianForm.tax)/100;
        this.fujianForm.total = total
        return total;
     }
  }
}
</script>



